import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import banner from '@/components/common-components/bannerSlide.vue';
let AppList = class AppList extends Vue {
    constructor() {
        super(...arguments);
        this.sessionId = '';
        this.appList = [
            {
                icon: 'iconshangpin',
                label: '商品管理',
                path: '/shopList'
                // path: '/commodityManageClass'
            },
            {
                icon: 'iconshangpin',
                label: '在线传单'
            },
            {
                icon: 'iconshangpin',
                label: '群发短信'
            },
            {
                icon: 'iconshangpin',
                label: '客户'
            },
            {
                icon: 'iconshangpin',
                label: '群活码'
            }
        ];
    }
    mounted() {
        this.sessionId = this.$route.query.sessionId;
    }
    appClick(item) {
        if (item.path) {
            this.$router.push({ path: item.path, query: { state: 'business@8a146790-f97e-11ea-8165-3111824eebe8' } });
        }
    }
};
AppList = __decorate([
    Component({
        components: {
            banner
        }
    })
], AppList);
export default AppList;
