import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
let pickUpMethod = class pickUpMethod extends Vue {
    constructor() {
        super(...arguments);
        this.selectedLabelDefault = '';
        this.tabs = [];
        this.isVisible = false;
        this.userInfo = {
            id: this.userId,
            name: '',
            phone: '',
            address: ''
        };
        this.options = {
            scrollToInvalidField: true,
            layout: 'fresh' // classic fresh
        };
        this.fields = [
            {
                type: 'input',
                modelKey: 'phone',
                label: '预留手机号',
                props: {
                    placeholder: '请输入您的手机号'
                },
                rules: {
                    required: true,
                    type: 'tel'
                },
                // validating when blur
                trigger: 'blur'
            },
            {
                type: 'input',
                modelKey: 'name',
                label: '姓名',
                props: {
                    placeholder: '请输入您的姓名'
                },
                rules: {
                    required: true
                },
                // validating when blur
                trigger: 'blur'
            },
            {
                type: 'textarea',
                modelKey: 'address',
                label: '配送地址',
                props: {
                    placeholder: '请输入您的地址'
                },
                rules: {
                    required: true
                },
                // validating when blur
                trigger: 'blur'
            }
        ];
    }
    get userId() {
        return this.$store.state.mtUserId;
    }
    get shopInfo() {
        return this.$store.state.shopInfo;
    }
    mounted() {
        this.initTransType();
        this.getUserInfo();
    }
    getUserInfo() {
        if (this.userId) {
            this.$store.dispatch('getMtUserInfo', { id: this.userId }).then((response) => {
                if (response.success) {
                    if (response.data) {
                        this.$set(this, 'userInfo', response.data);
                    }
                }
            });
        }
    }
    saveClick() {
        this.$refs.cubeForm.submit();
    }
    // 配送方式
    initTransType() {
        let transType = this.shopInfo.transType;
        let tabs = [];
        transType.forEach(type => {
            let label = '到店自取';
            if (type === 'ds') {
                label = '商家配送';
            }
            tabs.push({
                label,
                value: type
            });
        });
        this.selectedLabelDefault = tabs[0].value;
        this.$set(this, 'tabs', tabs);
    }
    validate() {
        Object.values(this.userInfo).some((value) => {
            if (!value) {
                this.isVisible = true;
                return true;
            }
        });
        return !this.isVisible;
    }
    maskClick() {
        this.hide();
    }
    hide() {
        if (!this.isVisible) {
            return;
        }
        this.isVisible = false;
    }
    show() {
        this.isVisible = true;
    }
    submitHandler(e) {
        e.preventDefault();
        let queryObj;
        queryObj = this.userInfo;
        this.$store.dispatch('addOrUpdateMtUser', queryObj).then((response) => {
            if (response.success) {
                this.hide();
            }
        });
    }
};
pickUpMethod = __decorate([
    Component({
        components: {},
        filters: {}
    })
], pickUpMethod);
export default pickUpMethod;
