import { __decorate } from "tslib";
import { Component, Vue, Watch } from 'vue-property-decorator';
import wx from 'weixin-js-sdk';
import NP from 'number-precision';
import previewOrder from '@/components/common-components/previewOrder.vue';
import payClick from '@/components/common-components/payClick.vue';
import pickUp from '@/components/common-components/pickUpMethod.vue';
let Home = class Home extends Vue {
    constructor() {
        super(...arguments);
        this.indicator = false;
        // 声明周期钩子
        this.model = { phone: '', openid: '' };
        this.actions = [
            {
                text: '¥00.00',
                action: 'moreMoney'
            },
            {
                type: 'button',
                text: '<div class="payButton">立即购买</div>',
                action: 'payButtonClickHandle'
            }
        ];
        // 已缴费用
        this.page = 1;
        this.commodityId = '';
        this.commodityObj = {};
        this.commodityNum = 1;
        this.totalCost = 0;
        this.userInfo = { 'phone': '', address: '' };
        this.openid = '';
        this.payButtonClick = false;
        this.weixinInfo = {};
        // 购物车控制
        this.payShowVisible = false;
    }
    activated() {
        this.$store.commit('SET_SESSION_ID', 'noSession');
        let userInfo = this.$route.query.userInfo;
        this.openid = this.$route.query.openid;
        this.$set(this, 'userInfo', userInfo);
        this.actions[0].text = '¥' + this.selectCommodityPrice;
    }
    get selectCommodityPrice() {
        let allPrice = 0;
        this.$store.state.selectCommodityList.forEach((item) => {
            allPrice += item.price * item.num;
        });
        return NP.strip(allPrice);
    }
    selectCommodityListChanged(newPrice) {
        this.actions[0].text = '¥' + newPrice;
    }
    getUserInfo() {
        let query = this.$route.query;
        if (query.code) {
            const toast = this['$createToast']({
                time: 0,
                txt: '数据获取中...'
            });
            toast.show();
            this.$store.dispatch('getCommodityUserInfo', { code: query.code }).then((response) => {
                if (response.success) {
                    if (response.data.openid) {
                        this.openid = response.data.openid;
                        this.getWeiXinConfig();
                        if (response.data.userInfo) {
                            this.userInfo = response.data.userInfo;
                        }
                        if (response.data.weixinInfo) {
                            this.weixinInfo = response.data.weixinInfo;
                        }
                        // this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
                    }
                }
                toast.hide();
            });
        }
        else {
            console.log('不是微信登陆');
        }
    }
    pageInit() {
        let query = this.$route.query;
        let state = query.state;
        if (state) {
            let stateArr = state.split('@');
            if (stateArr[0] === 'commodity') {
                this.commodityId = stateArr[1].replace(/\'/g, "");
            }
        }
        if (query.code) {
            this.getUserInfo();
        }
    }
    getCommodityById() {
        const toast = this['$createToast']({
            time: 0,
            txt: '数据获取中...'
        });
        toast.show();
        this.$store.dispatch('getCommodityById', { id: this.commodityId }).then((response) => {
            if (response.success) {
                this.$set(this, 'commodityObj', response.data);
            }
            toast.hide();
        });
    }
    fenToYuan(num) {
        if (typeof num !== 'number' || isNaN(num))
            return null;
        return '合计 ¥' + NP.strip((num / 100).toFixed(2));
    }
    getWeiXinConfig() {
        // let url = window.location.origin + window.location.pathname
        let url = window.location.href;
        this.$store.dispatch('getWeixinConfig', { url: url }).then((res) => {
            console.log('获取微信配置成功', res);
            if (res.success) {
                let config = res.data;
                wx.config({
                    debug: false,
                    appId: config.appId,
                    timestamp: config.timestamp,
                    nonceStr: config.nonceStr,
                    signature: config.signature,
                    jsApiList: ['chooseWXPay'] // 必填，需要使用的JS接口列表
                });
            }
            wx.ready(function () {
            });
            wx.error(function (err) {
            });
        });
    }
    clickHandler(item) {
        if (item.action) {
            this[item.action](item);
        }
    }
    get businessId() {
        return this.$store.state.businessId;
    }
    get shopInfo() {
        return this.$store.state.shopInfo;
    }
    get mtUserId() {
        return this.$store.state.mtUserId;
    }
    async payButtonClickHandle() {
        this.payButtonClick = true;
        let commodityList = [];
        this.$store.state.selectCommodityList.forEach((item) => {
            let obj = {};
            let fee = 0;
            obj.id = item.id;
            obj.name = item.name;
            obj.num = item.num;
            obj.price = item.price;
            obj.imgUrl = item.imgUrlList[0];
            commodityList.push(obj);
        });
        if (commodityList.length <= 0) {
            const toast = this.$createToast({
                txt: '请选择商品',
                type: 'error'
            });
            toast.show();
            return;
        }
        let isMini = await this.getIsMiniProgram();
        if (isMini) {
            let pickValidate = this.$refs.pickUp.validate();
            let pdType = this.$refs.pickUp.selectedLabelDefault;
            if (!pickValidate) {
                return;
            }
            let orderInfo = {
                fee: this.selectCommodityPrice,
                businessId: this.businessId,
                shopId: this.shopInfo.id,
                userId: this.mtUserId,
                pdType: pdType
            };
            wx.miniProgram.navigateTo({
                url: '/pages/jumbprocess/index?type=commodity&commodityList=' + JSON.stringify(commodityList) + '&orderInfo=' + JSON.stringify(orderInfo)
            });
            return;
        }
        // if (!this.userInfo.phone || !this.userInfo.address) {
        //   this.$router.push({ path: '/orderConfirm', query: { userInfo: this.userInfo, openid: this.openid } })
        // } else {
        //   let that = this
        //   let query: any = {}
        //   query.fee = this.selectCommodityPrice
        //   query.body = '集合订单'
        //   query.openid = this.openid
        //   // query.commodityId = this.commodityId
        //   query.commodityList = commodityList
        //   query.businessId = this.$store.state.businessId
        //   // query.num = this.commodityNum
        //   // query.subscribe = this.weixinInfo.subscribe
        //   this.$store.dispatch('setCommodityOrder', query).then((response) => {
        //     if (response.success) {
        //       let payData = response.data
        //       wx.chooseWXPay({
        //         timestamp: payData.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
        //         nonceStr: payData.nonceStr, // 支付签名随机串，不长于 32 位
        //         package: payData.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
        //         signType: payData.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
        //         paySign: payData.paySign, // 支付签名
        //         success: function (res) {
        //           // 支付成功后的回调函数
        //           that.payButtonClick = false
        //           const toast = that.$createToast({
        //             time: 2000,
        //             txt: '订单处理中...',
        //             mask: true,
        //             onTimeout: () => {
        //               // that.getBillListByHouseId()
        //               // that['$createDialog']({
        //               //   type: 'alert',
        //               //   content: '购买成功\n 请查看公众号订单信息',
        //               //   icon: 'cubeic-ok'
        //               // }).show()
        //               that.$router.replace({ path: '/paySuccess', query: { openid: that.openid } })
        //             }
        //           })
        //           toast.show()
        //         },
        //         error: function () {
        //         }
        //       })
        //     }
        //   })
        // }
    }
    shoppingClick() {
        this.payShowVisible = !this.payShowVisible;
    }
    payShowCancel() {
        this.payShowVisible = false;
    }
    async getIsMiniProgram() {
        return new Promise((resolve) => {
            wx.miniProgram.getEnv(function (res) {
                resolve(res.miniprogram);
            });
        });
    }
};
__decorate([
    Watch('selectCommodityPrice')
], Home.prototype, "selectCommodityListChanged", null);
Home = __decorate([
    Component({
        components: {
            previewOrder,
            payClick,
            pickUp
        },
        filters: {
            getFormatDate(date) {
                return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate();
            },
            fenToYuan(num) {
                if (typeof num !== 'number' || isNaN(num))
                    return null;
                return '¥' + (num / 100).toFixed(2);
            }
        }
    })
], Home);
export default Home;
